<template>
  <div>
    <a :href="linkHref" class="go" :title="linkTitle">{{ rowData.redirectSrcUrl }}</a>
  </div>
</template>

<script>
export default {
  props: {
    rowData: {
      type: Object,
      required: true
    },
    rowIndex: {
      type: Number
    }
  },
  computed: {
    linkHref: function () {
      let url = this.rowData.redirectSrcUrl;
      if (url === '') {
        return '';
      }
      let absoluteUrl = new RegExp('^(?:[a-z]+:)?//', 'i');
      if (!absoluteUrl.test(url) && !url.includes('$')) {
        url = Craft.getSiteUrl(Craft.trim(url, '/'));
      }

      return url;
    },
    linkTitle: function () {
      let title = '';

      title += ('User Agent: ' + this.rowData.userAgent + "\n\n");
      title += ('Message: ' + this.rowData.exceptionMessage + "\n\n");
      title += ('File Path: ' + this.rowData.exceptionFilePath + "\n\n");
      title += ('Line No.: ' + this.rowData.exceptionFileLine);

      return title;
    }
  }
}
</script>
